import {
  swapsData,
  swapsColumns,
  inputsData,
  inputsColumns,
  outputsData,
  outputsColumns,
  commentsData
} from '@/admin/store/mock/swapsMock'
import { crudStore } from '@/shared/plugins/vuex'
import { paginationCrud } from '@/admin/store/cruds/table'

export default crudStore([
  paginationCrud({
    tableName: 'inputs',
    totalCount: 123,
    data: inputsData,
    columns: inputsColumns,
    filters: {
      dateRange: {
        start: '',
        end: ''
      },
      search: '',
      status: null,
      sort: 'newest'
    }
  }),
  paginationCrud({
    tableName: 'outputs',
    totalCount: 123,
    data: outputsData,
    columns: outputsColumns,
    filters: {
      dateRange: {
        start: '',
        end: ''
      },
      search: '',
      status: null,
      sort: 'newest'
    }
  })
], {
  namespaced: true,
  state: {
    currentSwap: '',
    comments: {
      data: commentsData,
      filters: {
        search: '',
        dateRange: {
          start: '',
          end: ''
        }
      }
    },
    swaps: swapsData,
    columns: swapsColumns,
    filters: {
      dateRange: {
        start: '',
        end: ''
      },
      search: '',
      status: null,
      sort: 'newest'
    },
    perPage: 100,
    totalCount: 12300,
    page: 1
  },

  mutations: {
    SET_SWAPS (state, payload) {
      state.sendCoin = payload
    },
    SET_CURRENT_SWAP (state, payload) {
      state.currentSwap = payload
    },
    SET_COMMENT (state, payload) {
      state.comments.push(payload)
    },
    SET_COLUMNS (state, payload) {
      state.receiveCoin = payload
    },
    SET_FILTERS (state, payload) {
      state.filters = payload
    },
    SET_PER_PAGE (state, payload) {
      state.perPage = payload
    },
    SET_TOTAL_COUNT (state, payload) {
      state.totalCount = payload
    },
    SET_PAGE (state, payload) {
      state.page = payload
    }
  },

  getters: {
    getSwaps (state) {
      return state.swaps
    },
    getCurrentSwap (state) {
      return state.currentSwap
    },
    getColumns (state) {
      return state.columns
    },
    getComments (state) {
      return state.comments
    },
    getActiveColumns (state) {
      return state.columns.filter(item => item.isShow)
    },
    getPerPage (state) {
      return state.perPage
    },
    getFilters (state) {
      return state.filters
    },
    getPage (state) {
      return state.page
    },
    getTotalCount (state) {
      return state.totalCount
    }
  }
})
