<template>
  <AdminHeader>
    <Heading :to="{ name: ADMIN_ROUTES_NAMES.SWAPS }">{{ t('swaps.title') }}</Heading>
    <template v-if="itemId" #breadcrumbs>
      {{ itemId }}
    </template>
  </AdminHeader>
  <section class="swaps">
    <keep-alive>
      <component
        :is="itemId ? 'SwapsItem' : 'SwapsGrid'"
      />
    </keep-alive>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useRoute } from '@/shared/plugins/vue-router'
import { useGetters, useModule, useMutations } from '@/shared/plugins/vuex'
import { computed, defineAsyncComponent, watch } from 'vue'
import { ADMIN_ROUTES_NAMES } from '@/admin/common/routes'
import { MODULES_NAMES } from '@/shared/common/modules'
import Heading from '@/admin/components/Heading'
import AdminHeader from '@/admin/components/Header/AdminHeader'
import swapsModule from '@/admin/store/modules/swaps'

export default {
  name: 'Swaps',
  components: {
    AdminHeader,
    Heading,
    SwapsGrid: defineAsyncComponent(() => import('@/admin/views/Swaps/SwapsGrid/SwapsGrid')),
    SwapsItem: defineAsyncComponent(() => import('@/admin/views/Swaps/SwapsItem/SwapsItem'))
  },
  setup () {
    const { t } = useI18n()
    const route = useRoute()
    useModule(MODULES_NAMES.SWAPS, swapsModule)

    const {
      getCurrentSwap
    } = useGetters(MODULES_NAMES.SWAPS)
    const {
      SET_CURRENT_SWAP
    } = useMutations(MODULES_NAMES.SWAPS)

    const itemId = computed({
      get: () => getCurrentSwap.value,
      set: SET_CURRENT_SWAP
    })

    watch(() => route.params.id, () => {
      itemId.value = route.params.id || ''
    }, { immediate: true })

    return {
      t,
      itemId,
      ADMIN_ROUTES_NAMES
    }
  }
}
</script>

<style lang="scss">
.swaps {
  flex-grow: 1;
  padding-left: 40px;
  padding-top: 19px;
  padding-bottom: 65px;

  &__header {
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 36px;
  }
}
</style>
