<template>
  <header
    class="admin-header"
  >
    <div class="admin-header__body">
      <div class="admin-header__title">
        <slot />
      </div>
      <div v-if="$slots.breadcrumbs" class="admin-header__breadcrumbs">
        <Icon name="arrow-right" opacity="0.4" />
        <div class="admin-header__breadcrumbs-item">
          <slot name="breadcrumbs" />
        </div>
      </div>
    </div>
    <GlobalSearchIcon name="search" @click="setIsSearch" />
  </header>
</template>

<script>
import { useRouter } from '@/shared/plugins/vue-router'
import { useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import GlobalSearchIcon from '@/admin/components/GlobalSearch/GlobalSearchIcon'
import { ADMIN_ROUTES_NAMES } from '@/admin/common/routes'
import Icon from '@/shared/components/Icon/Icon'

export default {
  name: 'AdminHeader',
  components: { Icon, GlobalSearchIcon },
  setup () {
    const router = useRouter()
    const {
      SET_IS_SEARCH
    } = useMutations(MODULES_NAMES.GLOBAL_SEARCH)

    const setIsSearch = (flag) => {
      SET_IS_SEARCH(flag)
      router.push({
        name: ADMIN_ROUTES_NAMES.GLOBAL_SEARCH
      })
    }
    return {
      setIsSearch
    }
  }
}
</script>

